import React from 'react';
import { Routes, Route, Outlet } from "react-router-dom";
import { motion } from 'framer-motion';
import TitleM from '../component/text/title-m';
import TitleL from '../component/text/title-l';
import Topnav from '../component/topnav/topnav';
import PlaylistCardM from '../component/cards/playlist-card-m';
import styles from "./library.module.css";
import { PLAYLIST } from '../data';
//import { useData } from '../context/DataContext';

function Library() {
    return (
        <div className={styles.LibPage}>
            <Topnav tabButtons={true} />
            <div className={styles.Library}>
                <LibraryContent /> {/* Renderiza el contenido de la librería */}
            </div>
        </div>
    );
}

function LibraryContent() {

    //const { data } = useData();
    //const combinedPlaylists = [...data.playlists, ...data.artists]; //...data.discos

    return (
        <div>
            <Routes>
                <Route path="/" element={<PlaylistTab playlists={PLAYLIST} />} />
                <Route path="podcasts" element={<PodcastTab playlists={PLAYLIST} />} />
                <Route path="artists" element={<ArtistTab playlists={PLAYLIST} />} />
                <Route path="albums" element={<AlbumTab playlists={PLAYLIST} />} />
            </Routes>
            <Outlet /> {/* Esto renderiza el contenido de la ruta actual */}
        </div>
    );
}

function PlaylistTab({ playlists }) {
    return (
        <motion.div
            initial={{ opacity: 0, scale: 1.005 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 1, ease: "easeInOut" }}
        >
            <TitleL>Listas de reproducción</TitleL>
            <div className={styles.Grid}>
                {playlists.filter(item => item.link && item.type !== "promo" && item.type !== "djs") // no se ve sino tiene link o es type:promo o type:djs
                          .slice(2, 20).map((item) => (
                          <PlaylistCardM key={item.title} data={item} />
                ))}
            </div>
        </motion.div>
    );
}

function PodcastTab({ playlists }) {
    return (
        <motion.div
            initial={{ opacity: 0, scale: 1.01 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: .8, ease: "easeInOut" }}
        >
            <TitleL>Cast</TitleL>
            <div className={styles.Grid}>
                {playlists.filter(item => item.type === 'podcast' && item.link).map((item) => (
                    <PlaylistCardM key={item.title} data={item} />
                ))}
            </div>
        </motion.div>
    );
}

function ArtistTab({ playlists }) {
    return (
        <motion.div
            initial={{ opacity: 0, scale: 1.01 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: .8, ease: "easeInOut" }}
        >
            <TitleL>Djs</TitleL>
            <div className={styles.Grid}>
                {playlists
                    .filter(item => item.type === 'djs' && item.link) // Filtra los elementos que tienen un link
                    .map((item) => (
                        <PlaylistCardM key={item.title} data={item} />
                    ))}
            </div>
        </motion.div>
    );
}

function AlbumTab({ playlists }) {
    return (
        <motion.div
            initial={{ opacity: 0, scale: 1.01 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: .8, ease: "easeInOut" }}
        >
            <TitleL>Álbumes</TitleL>
            <div className={styles.Grid}>
                {playlists.filter(item => item.type === 'album').map((item) => (
                    <PlaylistCardM key={item.title} data={item} />
                ))}
            </div>
        </motion.div>
    );
}

export default Library;